.loading {
  background-color: #0003;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loadingContent {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
  .chasedot {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    animation: sk-chase-dot 2.0s infinite ease-in-out both;
    &::before {
      content: '';
      display: block;
      width: 25%;
      height: 25%;
      background-color: #fff;
      border-radius: 100%;
      animation: sk-chase-dot-before 2.0s infinite ease-in-out both;
    }
  }
}

.chasedot:nth-child(1) { animation-delay: -1.1s; }
.chasedot:nth-child(2) { animation-delay: -1.0s; }
.chasedot:nth-child(3) { animation-delay: -0.9s; }
.chasedot:nth-child(4) { animation-delay: -0.8s; }
.chasedot:nth-child(5) { animation-delay: -0.7s; }
.chasedot:nth-child(6) { animation-delay: -0.6s; }
.chasedot:nth-child(1):before { animation-delay: -1.1s; }
.chasedot:nth-child(2):before { animation-delay: -1.0s; }
.chasedot:nth-child(3):before { animation-delay: -0.9s; }
.chasedot:nth-child(4):before { animation-delay: -0.8s; }
.chasedot:nth-child(5):before { animation-delay: -0.7s; }
.chasedot:nth-child(6):before { animation-delay: -0.6s; }

@keyframes sk-chase {
  100% { transform: rotate(360deg); }
}

@keyframes sk-chase-dot {
  80%, 100% { transform: rotate(360deg); }
}

@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  } 100%, 0% {
    transform: scale(1.0);
  }
}
